@mixin shortcutSize($size) {
    .dot,
    .dot-internal,
    .dot-internal-b,
    .dot-internal-c {
        width: $size;
    }
    .dot-internal {
        &:before,
        &:after {width: ($size - 13);}
        .dot-internal-b,
        .dot-internal-c {&:after {width: ($size - 13);}}
    }


}

@mixin clearfix() {
    &:before, &:after {
        content: ' ';
        display: table;
    }

    &:after { clear: both; }
}

@mixin make-grid-columns($i: 1, $list: ".col-xs-#{$i}, .col-sm-#{$i}, .col-md-#{$i}, .col-lg-#{$i}") {
    @for $i from (1 + 1) through $grid-columns {
        $list: "#{$list}, .col-xs-#{$i}, .col-sm-#{$i}, .col-md-#{$i}, .col-lg-#{$i}";
    }

    #{$list} {
        position: relative;
        min-height: 1px;
        padding-left: ceil(($grid-gutter / 2));
        padding-right: floor(($grid-gutter / 2));
    }
}

@mixin float-grid-columns($class, $i: 1, $list: ".col-#{$class}-#{$i}") {
    @for $i from (1 + 1) through $grid-columns {
        $list: "#{$list}, .col-#{$class}-#{$i}";
    }
    #{$list} { float: left; }
}


@mixin calc-grid-column($index, $class, $type) {
    @if ($type == width) and ($index > 0) {
        .col-#{$class}-#{$index} { width: percentage(($index / $grid-columns)); }
    }

    @if ($type == push) and ($index > 0) {
        .col-#{$class}-push-#{$index} { left: percentage(($index / $grid-columns)); }
    }

    @if ($type == push) and ($index == 0) {
        .col-#{$class}-push-0 { left: auto; }
    }

    @if ($type == pull) and ($index > 0) {
        .col-#{$class}-pull-#{$index} { right: percentage(($index / $grid-columns)); }
    }

    @if ($type == pull) and ($index == 0) {
        .col-#{$class}-pull-0 { right: auto; }
    }

    @if ($type == offset) {
        .col-#{$class}-offset-#{$index} { margin-left: percentage(($index / $grid-columns)); }
    }
}

@mixin loop-grid-columns($columns, $class, $type) {
    @for $i from 0 through $columns {
        @include calc-grid-column($i, $class, $type);
    }
}

@mixin make-grid($class) {
    @include float-grid-columns($class);
    @include loop-grid-columns($grid-columns, $class, width);
    @include loop-grid-columns($grid-columns, $class, pull);
    @include loop-grid-columns($grid-columns, $class, push);
    @include loop-grid-columns($grid-columns, $class, offset);
}

@mixin container-fixed($gutter: $grid-gutter) {
    margin-right: auto;
    margin-left: auto;
    padding-left: floor(($gutter / 2));
    padding-right: ceil(($gutter / 2));

    @include clearfix;
}

@mixin make-row($gutter: $grid-gutter) {
    margin-left: ceil(($gutter / -2));
    margin-right: floor(($gutter / -2));

    @include clearfix;
}

@mixin make-xs-column($columns, $gutter: $grid-gutter) {
    position: relative;
    float: left;
    width: percentage(($columns / $grid-columns));
    min-height: 1px;
    padding-left: ($gutter / 2);
    padding-right: ($gutter / 2);
}

@mixin make-xs-column-offset($columns) { margin-left: percentage(($columns / $grid-columns)); }
@mixin make-xs-column-push($columns) { left: percentage(($columns / $grid-columns)); }
@mixin make-xs-column-pull($columns) { right: percentage(($columns / $grid-columns)); }

@mixin make-sm-column($columns, $gutter: $grid-gutter) {
    position: relative;
    min-height: 1px;
    padding-left: ($gutter / 2);
    padding-right: ($gutter / 2);

    @media (min-width: $screen-sm-min) {
        float: left;
        width: percentage(($columns / $grid-columns));
    }
}

@mixin make-sm-column-offset($columns) {
    @media (min-width: $screen-sm-min) { margin-left: percentage(($columns / $grid-columns)); }
}

@mixin make-sm-column-push($columns) {
