@mixin responsive-invisibility($parent) {
    #{$parent} {
        display: none !important;
    }
}

@mixin center-block() {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@mixin text-hide() {
    border: 0;
    font: 0/0 a;
    color: transparent;
    text-shadow: none;
    background-color: transparent;
}

@mixin list-unstyled {
    padding-left: 0;
    list-style: none;
}

@mixin img-responsive($display: block) {
    display: $display;
    max-width: 100%;
    height: auto;
}

@mixin text-overflow() {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

@mixin reset-text() {
    font-family: $font-family;
    font-style: normal;
    font-weight: normal;
    letter-spacing: normal;
    line-break: auto;
    line-height: $line-height;
    text-align: left;
    text-align: start;
    text-decoration: none;
    text-shadow: none;
    text-transform: none;
    white-space: normal;
    word-break: normal;
    word-spacing: normal;
    word-wrap: normal;
}

@mixin opacity($opacity) {
    opacity: $opacity;
    $opacity-ie: ($opacity * 100);
    filter: alpha(opacity=$opacity-ie);
}

@mixin responsive-font($responsive, $min, $max: false, $fallback: false) {
    $responsive-unitless: $responsive / ($responsive - $responsive + 1);
    $dimension: if(unit($responsive) == 'vh', 'height', 'width');
    $min-breakpoint: $min / $responsive-unitless * 100;

    @media (max-#{$dimension}: #{$min-breakpoint}) { font-size: $min; }

    @if $max {
        $max-breakpoint: $max / $responsive-unitless * 100;
        @media (min-#{$dimension}: #{$max-breakpoint}) { font-size: $max; }
    }

    @if $fallback {
        font-size: $fallback;
    }

    font-size: $responsive;
}

@mixin absolute-center($orientation: false) {
    position: absolute;

    @if $orientation {
        @if $orientation == vertical {
            top: 50%;
            transform: translateY(-50%);
        } @else {
            left: 50%;
            transform: translateX(-50%);
        }
    } @else {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

@mixin placeholder($color: $text-color) {
    &::-moz-placeholder { color: $color; opacity: 1; }
    &:-ms-input-placeholder { color: $color; }
    &::-webkit-input-placeholder { color: $color; }
}

@mixin create-movement-icons($i: 1) {
    @for $i from 1 through 15 {
        .movement-#{$i} > & {
            background-image: url('../images/movement-#{$i}.png');
        }
    }
}

@mixin icomoon-base() {
    font-family: 'ico-ftv' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
