.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus { outline: none; }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;

    &:before, &:after {
        content: "";
        display: table;
    }

    &:after { clear: both; }

    .slick-loading & { visibility: hidden; }
}

.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;

    img { display: block; }

    &.slick-loading img { display: none; }

    display: none;

    &.dragging img { pointer-events: none; }

    .slick-initialized & { display: block; }

    .slick-loading & { visibility: hidden; }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}

.slick-arrow {
    background-color: #000;
    color: #FFF;
    height: 38px;
    display: inline-block;
    padding: 0 15px;
    position: relative;
    line-height: 30px;
    &:before,
    &:after {
        content: " ";
        background-color: #000;
        height: 20px;
        width: 5px;
        position: absolute;
        top: 7px;
    }
    &:before {
        left: -5px;
    }
    &:after {
        right: -5px;
    }
    &:hover {
        background-color: $yellow;
        &:before,
        &:after {
            content: " ";
            background-color: $yellow;
        }
        .ico-arrow-left,
        .ico-arrow-right {
            .ico-arrow-left-internal,
            .ico-arrow-left-internal:before,
            .ico-arrow-left-internal:after,
            .ico-arrow-right-internal,
            .ico-arrow-right-internal:before,
            .ico-arrow-right-internal:after,
            &:before,
            &:after {
                background-color: #000;    
            }

            
        }
    }

    position: absolute;
    bottom: 0;
    z-index: 31;
    
    &.slick-next {
        right: 420px;
    }
    &.slick-prev {
        left: 20px;
    }
}

.slick-dots {
    li {
        background-color: #000;
        color: #FFF;
        height: 30px;
        display: inline-block;
        padding: 0 6px;
        margin: 0 10px;
        position: relative;
        line-height: 30px;
        button {
            color: #FFF;
            text-decoration: underline;
        }
        &:before,
        &:after {
            content: " ";
            background-color: #000;
            height: 20px;
            width: 5px;
            position: absolute;
            top: 5px;
        }
        &:before {
            left: -5px;
        }
        &:after {
            right: -5px;
        }
        &.slick-active,
        &:hover {
            background-color: $yellow;
            button {
                color: #000;    
            }
            &:before,
            &:after {
                content: " ";
                background-color: $yellow;
            }
            
        }
    }
        

    @media (min-width: $screen-md-min) {
    
    }

}

.slick-arrow.slick-hidden { display: none; }
